<template>
  <div class="pos">
      <div>
          <el-row>
        <!-- 操作栏 -->
            <el-col :span='7' class="order_menu" id="order_menu">
                
                <el-tabs>
                    <el-tab-pane label='点餐'>
                    
                        <el-table :data="tableData" style="width:100%;" border>
                            <el-table-column prop='goodsName' label='商品名称'></el-table-column>
                            <el-table-column prop='count' label='数量' width='50'></el-table-column>
                            <el-table-column prop='price' label='金额' width='70'></el-table-column>
                            
                            <el-table-column label='操作' width='100' fixed='right'>
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="collect_count">
                            <small>数量:{{totalCount}}</small>
                            <small>金额:{{totalMoney}}</small>
                        </div>

                        <div class="collect_btn">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger">删除</el-button>
                            <el-button type="success">结账</el-button>
                        </div>

                    </el-tab-pane>

                    <el-tab-pane label='挂单'>
                        挂单
                    </el-tab-pane>

                    <el-tab-pane label='外卖'>
                        外卖
                    </el-tab-pane>
                </el-tabs>

                

            </el-col>

            <!-- 商品栏 -->
            <el-col :span='17'>

                <div class="goods_wrap" >
                    <div class="goods_title">常用商品</div>
                    <div class="goods_list">
                        <ul>
                            <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                                <span>{{goods.goodsName}}</span>
                                <span class="goods_price">¥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="goods_type">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            
                            <div>
                                <ul class='cookList'>
                                    <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>    
                        
                        </el-tab-pane>

                        <el-tab-pane label="小食">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>  
                        </el-tab-pane>

                        <el-tab-pane label="饮料">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>  
                        </el-tab-pane>

                        <el-tab-pane label="套餐">
                            <div>
                                <ul class='cookList'>
                                    <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </div>  
                        </el-tab-pane>
                    </el-tabs>

                </div>
            </el-col>

        </el-row>
      </div>
  </div>
</template>

<script>
    import axios from 'axios';
    export default{
        name:'Pos',
        data(){
            return{
                tableData: [],
                oftenGoods:[],
                type0Goods:[],
                type1Goods:[],
                type2Goods:[],
                type3Goods:[],
                totalCount:0,
                totalMoney:0
            }
        },
        created(){
            axios.get('http://jspang.com/DemoApi/oftenGoods.php')
            .then(response=>{
                this.oftenGoods=response.data;
            })
            .catch(error=>{
                alert('网络错误')
            })


            axios.get('http://jspang.com/DemoApi/typeGoods.php')
            .then(response=>{
                this.type0Goods=response.data[0];
                this.type1Goods=response.data[1];
                this.type2Goods=response.data[2];
                this.type3Goods=response.data[3];
            })
            .catch(error=>{
                alert('网络错误，不能访问');
            })

        },
        mounted:function(){
            var orderHeight=document.body.clientHeight;
            document.getElementById('order_menu').style.height=orderHeight+'px';
        },
        methods:{
            addOrderList(goods){
                // 初始化数量和金额
                this.totalCount=0;
                this.totalMoney=0; 

                let isHave=false;
                // 判断商品中是否存在于订单列表
                for(let i=0;i<this.tableData.length;i++){
                    if(this.tableData[i].goodsId==goods.goodsId){
                        isHave=true;//存在
                    }
                }

                //如果存在
                if(isHave){
                    let arr=this.tableData.filter(o =>o.goodsId==goods.goodsId);
                    arr[0].count++;
                // 如果不存在
                }else{
                    let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
                    this.tableData.push(newGoods);
                }

                //计算
                this.tableData.forEach((element)=>{
                    this.totalCount+=element.count;
                    this.totalMoney=this.totalMoney+(element.count*element.price);

                    this.getAllMoney();
                })
            },
            delSingleGoods(goods){
                this.tableData=this.tableData.filter(o=>o.goodsId !=goods.goodsId);
                this.getAllMoney();
            },
            
            //汇总数量和金额
            getAllMoney(){
                this.totalCount=0;
                this.totalMoney=0;
                if(this.tableData){
                        this.tableData.forEach((element) => {
                    this.totalCount+=element.count;
                    this.totalMoney=this.totalMoney+(element.price*element.count);   
                });
            }
                
            }
        }
    }
</script>

<style>
    .order_menu{
        background: #eff2f7;
        border-right: 1px solid #cccccc;
    }

    .collect_btn{
        margin-top: 10px;
    }

    .goods_wrap{
        width: 100%
    }

    .goods_title{
       height: 20px;
       border-bottom:1px solid #D3DCE6;
       background-color: #F9FAFC;
       padding:10px;
       text-align: left;
    }
    .goods_list ul li{
      list-style: none;
      float:left;
      border:1px solid #E5E9F2;
      padding:10px;
      margin:5px;
      background-color:#fff;
      border-radius: 5px;
      cursor: pointer;
   }
  .goods-price{
      color:#58B7FF; 
   }

   .goods_type{
       float: left;
       width: 100%;
    
   }
   .cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
 
   }
   .cookList li span{
       
        display: block;
        float:left;
        font-size: 16px;
        width: 100%;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }

   .collect_count{
       width: 100%;
       height: 40px;
       line-height: 40px;
   }
   .collect_count small{
       margin: 0 20px;
   }
</style>



